.foodlistWrapper{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin-top: 15px;
    align-items: center;
    width: 100%;
    height: 100%;
}
 .foodlist{
    border: 2px solid rgb(34, 197, 107);
    width: 300px;
    height: 240px;
    border-radius: 5px;
    display:flex;
    flex-direction: column;
    margin:10px;
    box-sizing: border-box;
}
.foodlist img{
    box-sizing: border-box;
    width: 100%;
    height: 180px;
}
.namePrice{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.namePrice .foodname{
   color: white;
   font-weight: 600;
   border: 1px solid rgb(184, 182, 182);
   border-radius: 4px;
   padding: 5px;
}
.namePrice .foodname:hover{
      background-color: rgb(23, 109, 60);
}

.foodprice{
    color: rgb(220, 33, 20);
    font-weight: 600;
    font-size: 18px;
}

@media only screen 
       and (min-device-width: 280px)
       and (max-device-width:480px){
        .foodlistWrapper{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
        }
    .foodlist{
        border: 2px solid rgb(34, 197, 107);
        width: 300px;
        height: 240px;
        display: block;
        margin: 35px auto;
        box-sizing: border-box;
        border-radius: 5px;
    }
    .foodlist img{
      width: 100%;
      height: 100%;
    }
    .namePrice{
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    .namePrice .foodname{
       color: white;
       font-weight: 600;
       border: 1px solid rgb(184, 182, 182);
       padding: 4px;
    }
    .namePrice .foodname:hover{
        background-color: rgb(23, 109, 60);
  }
    .foodprice{
        color: rgb(220, 33, 20);
        font-weight: 600;
        font-size: 18px;
    }
}
footer{
    display: flex;
    align-items: center;
    justify-content: center;
}
footer p{
    color: rgb(61, 245, 205);
    font-weight: 700;
}
 a{
    color: rgb(245, 248, 245);
    background-color: rgb(255, 47, 144);
    border-radius: 50%;
    text-decoration: none;
    list-style: none;
 }
footer span{
    margin-left: 10px;
    margin-right: 10px;
    font-size: 25px;
    color: rgb(245, 248, 245);
    background-color: rgb(255, 47, 144);
    border-radius: 50%;
    transform: translateY(-10px);
    cursor: pointer;
    -webkit-animation: fly 0.8s linear infinite;
            animation: fly 0.8s linear infinite;
}
@-webkit-keyframes fly{
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(-10px);
    }
    100%{
        transform: translateY(0px);
    }
}
@keyframes fly{
    0%{
        transform: translateY(0px);
    }
    50%{
        transform: translateY(-10px);
    }
    100%{
        transform: translateY(0px);
    }
}
